@import "../../helpers/style/variables";
@import "../../helpers/style/mixins";


.main-product-details {

  &__direction {
    margin-bottom: 40px;
  }

  &__back {
    margin-bottom: 16px;
  }

  &__main {
    @include page-grid;
  }

  &__title {
    grid-column: 1 / -1;
    margin: 0;
    margin-bottom: 40px;
  }

  &__photos {
    grid-column: 1 / 13;
  }

  &__brief-info {
    grid-column: 14 / 21;
  }
}
